// Variables
// ---------
$body-font-size           : 16px;
$body-font                : 'Roboto', sans-serif;
$body-line-height         : (24 / 16);
$body-color               : #464646;
$body-background          : #FFF;


// Table
$table-header-font-weight : 700;
$table-cell-padding       : 5px 10px;

// Text
$heading-font-weight      : 700;
$heading-line-height      : 1.2;
$heading-margin           : 0 0 1em;
$paragraph-margin         : 0 0 1em;
$list-margin              : 1em 0;
$h1                       : 28px;
$h2                       : 24px;
$h3                       : 20px;
$h4                       : 18px;
$h5                       : 16px;
$bold-font-weight         : 700;
$space-1                  : 1em;
$space-2                  : 2em;
$space-3                  : 3em;
$text-small               : 14px;

// Color
$main                     : $body-color;
$red                      : #E81313;
$link-color               : #009EB5;
$link-color-hover         : #00839A;
